.dash{
  width: 100%;
  min-height: 65vh;
  padding: 1rem 0;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  align-items: flex-start;
}

.dash--storybook-test{
  background-color: rgb(112, 195, 228);
  min-height: 100vh;
}

#ind-add{
  max-width: 12rem;
  border: 3px dashed rgb(175, 175, 175);
  background: rgba(255, 255, 255, 0.95);
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
}

.card{
  width: 16rem;
  min-height: 15rem;
  margin: 1rem 1rem;
  border: 1px solid rgb(228, 228, 228);
  border-radius: 5px;
  background:  rgb(255, 255, 255);
  box-shadow: 4px 4px 3px rgba(0, 0, 0, 0.1);
  display: grid;
  grid-template-rows: auto 2fr auto;
  grid-template-areas:
    "header"
    "card-content"
    "action"
  ;
}

.card__header{
  grid-area: header;
  padding: 0 1rem;
  border-bottom: 1px solid rgb(228, 228, 228);
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
}

.card__content{
  grid-area: card-content;
  padding: 0.5rem 1rem;
}

.card__action{
  grid-area: action;
  float: bottom;
}

@media (min-width: 1024px) {
  .card{
    width: 22rem;
  }
}

@media (min-width: 1440px) {
  .card{
    width: 24rem;
  }
}
:root{
  --primaryBlue: #2196F3;
  --primaryActBlue: #1d7cca;
  --info: #00BCD4;
  --infoAct: #0098ac;
  --danger: #F44336;
  --dangerAct: #c5382e;
  --warn: #FFEB3B;
  --warn: #dbc605;
  --success: #4CAF50;
  --success: #3c8b3f;
}


.btn{
  z-index: 1;
  border: 0 solid transparent;
  border-radius: 4px;
  padding: 6px 12px;
  margin: 4px 8px;
  color: white;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-size: 1rem;
}

.btn:not(.btn--close){
  box-shadow: 6px 6px 3px rgba(0, 0, 0, 0.1);
}

.btn--std{
  background: #2196F3;
  background: var(--primaryBlue);
}
.btn--std:hover{
  background: #1d7cca;
  background: var(--primaryActBlue);
}
.btn--std:active{
  background: #1d7cca;
  background: var(--primaryActBlue);
  box-shadow: 4px 4px 3px rgba(0, 0, 0, 0.1);
}

.btn--submit{
  background: #2196F3;
  background: var(--primaryBlue);
}
.btn--submit:hover{
  background: #1d7cca;
  background: var(--primaryActBlue);
}
.btn--submit:active{
  background: #1d7cca;
  background: var(--primaryActBlue);
  box-shadow: 4px 4px 3px rgba(0, 0, 0, 0.1);
}

.btn--info{
  background: #00BCD4;
  background: var(--info);
}
.btn--info:hover{
  background: #0098ac;
  background: var(--infoAct);
}
.btn--info:active{
  background: #0098ac;
  background: var(--infoAct);
  box-shadow: 4px 4px 3px rgba(0, 0, 0, 0.1);
}

.btn--close{
  color: grey;
  background: transparent;
  border: 0 solid transparent;
  font-size: 1.5rem;
}

.heading{
  font-family: 'Helvetica', Arial sans-serif;
}
/*
  NOTE Modal BASE CSS
  i.e bare minimum, no custom modifiers
*/

.modal-wrapper{
  z-index: 5;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(71, 71, 71, 0.35);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-wrapper ~ *{
  -webkit-filter: blur(5px);
          filter: blur(5px);
}

.modal{
  width: 40vw;
  border-radius: 0.5rem;
  background: white;
  box-shadow: 4px 4px 3px rgba(0, 0, 0, 0.25);
}

.modal__header{
  border-bottom: 1px solid rgb(194, 194, 194);
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
}

.heading--modal{
  margin: 1.2rem 0.76rem;
  padding: 0.25rem 1rem;
}

.modal__content{
  padding: 0 1rem;
}
